
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Blur from 'vux/src/components/blur/index.vue'

import Card from 'vux/src/components/card/index.vue'

import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Countup from 'vux/src/components/countup/index.vue'

import Emotion from 'vux/src/components/wechat-emotion/index.vue'

import Toast from 'vux/src/components/toast/index.vue'



const baseList = [{
  url: 'javascript:',
  img: 'http://mamaj-oss.oss-cn-beijing.aliyuncs.com/free/Jia/luchun/1111.jpg',
  title: '可爱的小媳妇'
}, {
  url: 'javascript:',
  img: 'http://mamaj-oss.oss-cn-beijing.aliyuncs.com/free/Jia/luchun/3333.jpg',
  title: '漂亮的小媳妇'
}, {
  url: 'javascript:',
  img: 'http://mamaj-oss.oss-cn-beijing.aliyuncs.com/free/Jia/luchun/888.jpg',
  title: '爱我的小媳妇'
}]

const urlList = baseList.map((item, index) => ({
  url: 'http://htmlpreview.github.io/?https://github.com/jialuchun/lovefeng/blob/master/love.html',
  img: item.img,
  title: `(可点击)${item.title}`
}))


const only2ClickList = baseList.slice(0, 2).map(item => {
  item.url = 'http://m.baidu.com'
  return item
})

export default {
  directives: {
    TransferDom
  },
  components: {
    Swiper,
    SwiperItem,
    GroupTitle,
    XButton,
    Divider,
    Blur,
    Flexbox,
    FlexboxItem,
    Card,
    Divider,
    Confirm,
    Group,
    XSwitch,
    Countup,
    Emotion,
    Toast
  },
  ready () {

  },
  methods: {
    onSwiperItemIndexChange (index) {
      console.log('demo item change', index)
    },
    demo01_onIndexChange (index) {
      this.demo01_index = index
    },
    demo05_onIndexChange (index) {
      this.demo05_index = index
    },
    demo05_onLoad (id) {
      this.demo05_list = id === 1 ? baseList : demoList
    },
    demo06_onIndexChange (index) {
      this.demo06_index = index
    },
    demo07_onIndexChange (index) {
      this.demo07_index = index
    },
    onCancel () {
      console.log('on cancel')
    },
    onConfirm () {
      this.showPosition();
      this.doStart = true;
      console.log('on confirm')
    },
    onHide () {
      console.log('on hide')
    },
    onShow () {
      console.log('on show')
    },
    showPlugin () {
      this.show = true;
    },
    showPlugin2 () {
      this.show2 = true;
    },
    showPosition () {
      this.showPositionValue = true
    },
  },
  data () {
    return {
      showPositionValue: false,
      demo06_list: urlList,
      images: [
        'https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg',
        'https://o3e85j0cv.qnssl.com/waterway-107810__340.jpg',
        'https://o3e85j0cv.qnssl.com/hot-chocolate-1068703__340.jpg'
      ],
      url: 'https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg',
      show: false,
      show2: false,
      doStart: false,
      list1: ['亲亲','亲亲','亲亲','亲亲','亲亲'],
      list2: ['玫瑰','玫瑰','玫瑰','玫瑰','玫瑰'],
      list3: ['示爱','示爱','示爱','示爱','示爱'],
      list4: ['爱心','爱心','爱心','爱心','爱心'],
      list5: ['拥抱','拥抱','拥抱','拥抱','拥抱']
    }
  },
  computed: {
    'loveDay': function () {
      var time=Date.parse(new Date());
      var lasttime=Date.parse("2016-03-28");
      var day=parseInt((time-lasttime)/(1000*60*60*24));
      return day;
    },
    'babyDay': function () {
      var time=Date.parse(new Date());
      var lasttime=Date.parse("2017-11-26");
      var day=parseInt((lasttime-time)/(1000*60*60*24));
      return day;
    },
    'myDay': function () {
      var time=Date.parse(new Date());
      var lasttime=Date.parse("2017-12-09");
      var day=parseInt((lasttime-time)/(1000*60*60*24));
      return day;
    },
    'betweenDay': function () {
      var time=Date.parse(new Date());
      var lasttime=Date.parse("2017-06-01");
      var day=parseInt((time-lasttime)/(1000*60*60*24));
      return day;
    }
  },
  mounted () {
    
  }
}



